import { useState } from "react";
import {Form, FormControl} from 'react-bootstrap';
import {NavLink} from "react-router-dom";

//подключение стилей
import '../assets/css/fonts.css';
import '../assets/css/main.css';

//подключение картинок
import logo from '../assets/images/icons/xbox_logo.svg';
import user_logo from '../assets/images/icons/user.svg';
import drop from '../assets/images/icons/drop.svg';
import burgermenu from '../assets/images/burger.png';


export default function Header() {
    const [menuActive,setMenuActive] = useState(false);

        return(
                <header>
                    <div className="header_logo">
                        <NavLink to="/" exact>
                            <img src={logo} alt="XBOX logo"/>
                        </NavLink>
                    </div>

                    <div className="burger">
                        <img src={burgermenu} alt="burger" onClick={() => setMenuActive(!menuActive)} />
                    </div>

                    <nav className={menuActive ? 'active' : ''} onClick={()=>setMenuActive(false)} > 
                        <ul className="nav__topmenu">
                            <li> <a href="#" className="drop">Games <img src={drop} alt="drop"/> </a>
                                <div className="dropdown">                                 
                                    <ul className="nav__submenu">
                                        <li><NavLink to="/consolegames">Console Games</NavLink></li>
                                        <li><NavLink to="/pcgames">PC Games</NavLink></li>
                                    </ul>
                                </div>
                            </li>
                            <li><a href="#" className="drop">Consoles <img src={drop} alt="drop"/></a>
                                <div className="dropdown">
                                    <ul className="nav__submenu">
                                        <li><NavLink to="/consoles">Explore Games</NavLink></li>
                                        <li><NavLink to="/accessories">Accessories</NavLink></li>
                                    </ul>
                                </div>
                            </li>
                            <li><NavLink to="/community" exact>Community</NavLink></li>
                        </ul>
                        
                        <div className="header_serch">
                            <Form inline>
                                <FormControl type="text" placeholder="search"/>
                            </Form>
                        </div>
                        
                        <div className="header_login">
                            <img src={user_logo} alt="icon user"/>
                            <ul className="nav__topmenu">
                                <li><a href="#" className="drop">My Xbox <img src={drop} alt="drop"/></a>
                                    <div className="dropdown">
                                        <ul className="nav__submenu">
                                            <li><a href="/auth">Register</a></li>
                                            <li><a href="/login">Login</a></li>
                                        </ul>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </header> 
        )
    }

